html {
  font-family: "Noto Serif JP", serif;
  font-size: 0.5208333333vw;
}

body {
  overflow-x: hidden;
  font-family: "Noto Serif JP", serif;
  background-image: url("../images/bg@2x.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

body.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

picture {
  display: block;
}

a[href^="tel:"] {
  pointer-events: none;
}

@keyframes scroll-lead {
  0% {
    transform: scale(0, 1);
    transform-origin: 0 0;
  }

  50% {
    transform: scale(1, 1);
    transform-origin: 0 0;
  }

  51% {
    transform: scale(1, 1);
    transform-origin: 100% 0;
  }

  100% {
    transform: scale(0, 1);
    transform-origin: 100% 0;
  }
}

@keyframes bgextendAnimeBase {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes bgextendAnimeSecond {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeUpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes rotateAnime {
  from {
    transform: rotateZ(0deg);
  }

  to {
    transform: rotateZ(360deg);
  }
}

@keyframes zoomUpAnime {
  0% {
    opacity: 1;
    transform: rotateZ(0deg) scale(1);
  }

  30% {
    opacity: 1;
    transform: rotateZ(360deg) scale(1);
  }

  to {
    opacity: 0;
    transform: rotateZ(360deg) scale(20);
  }
}

.happiness {
  width: 100%;
  padding: 91px 0 57px;
  background-image: url("../images/kodawari-bg@2x.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.happiness__title {
  margin-top: -55px;
}

.happiness__inner {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}

.happiness__items {
  display: flex;
  flex-direction: row-reverse;
  gap: 40px;
  margin-right: 30px;
}

.happiness__link {
  margin-right: 80px;
  margin-top: -300px;
}

.happiness__link-tab {
  display: none;
}

.tategaki-comp__title {
  font-size: 32px;
  color: #ffffff;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.1em;
  position: relative;
  white-space: nowrap;
  font-feature-settings: "palt";
  font-feature-settings: "pkna";
}

.tategaki-comp__title span {
  display: inline-block;
  width: 84px;
  aspect-ratio: 1/1;
  position: absolute;
  bottom: 101%;
  left: 50%;
  transform: translateX(-50%);
}

.tategaki-comp__title span img {
  height: auto;
}

.tategaki-comp__text-box {
  font-size: 16px;
  color: #ffffff;
  font-weight: 600;
  line-height: 2;
  letter-spacing: 0.1em;
  height: 520px;
  margin-right: 24px;
}

.news-sec {
  margin-top: 198px;
}

.news-sec__inner {
  display: flex;
}

.news-sec__title {
  position: relative;
}

.news-sec__title span {
  display: block;
  width: 366px;
  aspect-ratio: 366/382;
  position: absolute;
  top: -31%;
  left: -210%;
}

.news-sec__title span img {
  height: auto;
}

.news-sec__items {
  width: 74.6753246753%;
  display: flex;
  flex-direction: column;
  margin-left: 120px;
}

.news-item {
  border-top: solid 1px #cccccc;
  background-color: transparent;
  transition: background-color 0.3s ease;
}

.news-item:hover {
  background-color: rgba(191, 140, 78, 0.1);
  transition: background-color 0.3s ease;
}

.news-item:last-child {
  border-bottom: solid 1px #cccccc;
}

.news-item a {
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.news-item__body {
  font-feature-settings: "palt";
}

.news-item__time {
  font-size: 16px;
  color: #333333;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
}

.news-item__title {
  font-size: 16px;
  color: #333333;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  margin-top: 19px;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.news-item__arrow {
  display: block;
  width: 12px;
  aspect-ratio: 1/2;
}

.news-item__arrow img {
  height: auto;
}

.menu-sec {
  margin-top: 200px;
}

.menu-sec__top {
  display: flex;
  flex-direction: row-reverse;
  position: relative;
}

.menu-sec__top span {
  display: block;
  width: 694px;
  aspect-ratio: 694/444;
  position: absolute;
  top: 0;
  left: -20%;
}

.menu-sec__top span img {
  height: auto;
}

.menu-sec__menu-list {
  display: flex;
  flex-direction: row-reverse;
  gap: 24px;
  margin-right: 120px;
}

.menu-sec__menu-item a {
  font-size: 20px;
  color: #333333;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.02em;
  writing-mode: vertical-rl;
  display: block;
  padding: 10px 6px;
  background-color: #ffffff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.menu-sec__menu-item a:hover {
  color: #ffffff;
  background-color: #bf8c4e;
  transition: all 0.3s ease;
}

.button-tab a {
  width: 100%;
  font-size: 3.6rem;
  color: #591902;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.02em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5.2rem;
  border-radius: 100vh;
  border: solid 2px #591902;
  position: relative;
}

.button-tab a:hover span {
  right: 25%;
  transition: all 0.3s ease;
}

.button-tab a span {
  display: block;
  width: 1.6rem;
  height: 3.2rem;
  position: absolute;
  top: 50%;
  right: 27%;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}

.tategaki-button a {
  font-size: 20px;
  color: #ffffff;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.1em;
  padding: 50px 25px;
  background-color: transparent;
  border: solid 2px #ffffff;
  transition: all 0.3s ease;
}

.tategaki-button a:hover {
  background-color: #ffffff;
  color: #591902;
  transition: all 0.3s ease;
}

.section__title_size_s {
  font-size: 48px;
  color: #ffffff;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0.1em;
  white-space: nowrap;
  font-feature-settings: "palt";
  font-feature-settings: "pkna";
}

.section__title_belt_lr {
  font-size: 60px;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.1em;
  background-color: #591902;
  white-space: nowrap;
  font-feature-settings: "palt";
  font-feature-settings: "pkna";
  padding: 31px 14px;
  position: relative;
  writing-mode: vertical-lr;
  text-orientation: upright;
}

.section__title_belt_lr:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: transparent;
  border: solid 1px #bf8c4e;
  z-index: -1;
}

.section__title_belt_rl {
  font-size: 60px;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.1em;
  background-color: #591902;
  white-space: nowrap;
  font-feature-settings: "palt";
  font-feature-settings: "pkna";
  padding: 31px 14px;
  position: relative;
  writing-mode: vertical-rl;
  text-orientation: upright;
}

.section__title_belt_rl:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: transparent;
  border: solid 1px #bf8c4e;
  z-index: -1;
}

.cta__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 260px;
}

.cta__belt {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.016em;
  display: block;
  text-align: center;
  background-color: #bf8c4e;
  padding: 2px 0;
}

.cta__tel {
  font-size: 32px;
  color: #bf8c4e;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.032em;
  margin-top: 4px;
}

.cta__tel span {
  font-size: 24px;
  color: #bf8c4e;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.032em;
}

.cta-button {
  display: none;
}

.cta-button__inner {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cta-button__icon {
  width: 5.3rem;
  aspect-ratio: 53/69;
}

.cta-button__icon img {
  height: auto;
}

.cta-button__tel {
  font-size: 3.2rem;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  margin-top: 2rem;
}

.container {
  width: 1232px;
  margin: 0 auto;
}

.wide-container {
  width: 1760px;
  margin: 0 auto;
}

@media only screen and (max-width: 1760px) {
  .wide-container {
    width: 94%;
